* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.link, h1 {
  font-size: 2.5rem;
}

:root {
  --deep-dark: #1a1a1a;
  --light-dark: #4f525c;
  --light-blue: #0084ff;
  --dark-blue: #0254a1;
  --light: #f5f5f5;
}

body {
  background-color: var(--light);
  justify-content: center;
  margin-top: 120px;
  font-family: Montserrat, sans-serif;
  display: flex;
}

h1 {
  color: var(--light);
  text-transform: uppercase;
  margin-bottom: 20px;
}

.fas {
  pointer-events: none;
}

.todo {
  width: 800px;
  text-align: center;
  background: #fff;
  border-radius: 5px;
  box-shadow: 4px 4px 5px rgba(82, 82, 82, .4);
}

.header {
  background-image: linear-gradient(340deg, #045de9 0%, #09c6f9 100%);
  border-radius: 5px 5px 0 0;
  padding: 40px;
}

.todo-input {
  width: 80%;
  color: var(--light-dark);
  background-color: var(--light);
  border: none;
  border-radius: 5px;
  margin-right: 20px;
  padding: 10px 20px;
}

.error-info {
  color: var(--dark-blue);
  margin-top: 20px;
}

.btn-add {
  color: var(--deep-dark);
  background-color: var(--light);
  text-transform: uppercase;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 10px 22px;
  transition: color .3s, background-color .3s;
}

.btn-add:hover {
  color: #fff;
  background-color: var(--light-dark);
}

.todolist {
  padding: 20px 0;
}

.todolist h3 {
  text-transform: uppercase;
  margin-bottom: 16px;
}

.todolist li {
  text-align: left;
  background-color: var(--light);
  justify-content: space-between;
  align-items: center;
  margin: 15px;
  padding-left: 20px;
  list-style: none;
  display: flex;
}

.tools {
  display: inline-block;
}

.tools button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 15px 10px;
  transition: background-color .3s;
}

.tools button:hover {
  background-color: #ddd;
}

.complete {
  color: var(--light-blue);
}

.edit {
  color: #0fb345;
  font-weight: bold;
}

.delete {
  color: tomato;
}

.completed {
  color: var(--light-dark);
  font-size: 12px;
  font-style: italic;
  text-decoration: line-through;
}

.popup {
  width: 800px;
  color: var(--light);
  background-color: var(--light-dark);
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: none;
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.popup h3 {
  padding: 20px;
}

.popup-info {
  color: var(--light);
  margin-bottom: 20px;
}

.popup-body {
  width: 90%;
  flex-wrap: wrap;
  justify-content: center;
  padding: 10px;
  display: flex;
}

.popup-input {
  width: 100%;
  color: var(--light-dark);
  background-color: var(--light);
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
}

.popup-btn {
  width: 30%;
  color: var(--deep-dark);
  background-color: var(--light);
  text-transform: uppercase;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin: 20px;
  padding: 10px 22px;
  transition: color .3s, background-color .3s;
  display: inline-block;
}

.popup-btn:hover {
  color: #fff;
  background-color: var(--light-blue);
}

